body {
  margin: 0;
  font: 100% "Lato", sans-serif;
  color: #333;
  background: #F8CDDA;
  /* fallback for old browsers */
}

.loginHeader {
  position: relative;
  text-align: center;
  background: linear-gradient(60deg, #283048 0%, #859398 100%);
  color: white;
  /* Animation */
  /*Shrinking for mobile*/
}
.loginHeader h1 {
  text-align: center;
  color: lightgrey;
  font: 100% "Lato", sans-serif;
  font-weight: 300;
  letter-spacing: 2px;
  font-size: 48px;
  padding: 0;
  margin: 0;
}
.loginHeader .inner-header {
  height: 85vh;
  width: 100%;
  margin: 0;
  padding: 0;
}
.loginHeader .flex {
  /*Flexbox for containers*/
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.loginHeader .wrapper {
  -webkit-animation: zoomin 2s;
          animation: zoomin 2s;
}
.loginHeader form {
  margin-top: 10px;
}
.loginHeader label {
  display: inline-block;
  width: 100px;
  text-align: left;
  margin-top: 2px;
}
.loginHeader input {
  border-style: solid;
  border-color: white;
}
.loginHeader input[type=password] {
  box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.2);
  margin-bottom: 20px;
}
.loginHeader #register-link {
  display: inline;
  width: 200px;
}
.loginHeader #register-link:hover {
  cursor: pointer;
  border-bottom: 1px solid white;
}
.loginHeader .button-container {
  width: 250px;
  height: 100px;
  margin: 10% auto;
  color: white;
  font-size: 1rem;
}
.loginHeader .button {
  position: relative;
  font-size: 1.5rem;
  border: 1.5px solid white;
  border-radius: 100px;
  width: 40px;
  height: 40px;
  padding: 5px;
  margin: auto;
  -webkit-transition: 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  transition: 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
.loginHeader .icon {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.loginHeader .button:hover {
  cursor: pointer;
  width: 125px;
  background-color: white;
  box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.2);
  color: #566270;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.loginHeader .button:active {
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
  -webkit-transition: 0.05s;
  transition: 0.05s;
}
.loginHeader .waves {
  position: relative;
  width: 100%;
  height: 15vh;
  margin-bottom: -7px;
  min-height: 100px;
  max-height: 150px;
}
.loginHeader .content {
  position: relative;
  height: 20vh;
  text-align: center;
  background-color: white;
}
.loginHeader .parallax > use {
  -webkit-animation: move-forever 25s cubic-bezier(0.55, 0.5, 0.45, 0.5) infinite;
          animation: move-forever 25s cubic-bezier(0.55, 0.5, 0.45, 0.5) infinite;
}
.loginHeader .parallax > use:nth-child(1) {
  -webkit-animation-delay: -2s;
          animation-delay: -2s;
  -webkit-animation-duration: 7s;
          animation-duration: 7s;
}
.loginHeader .parallax > use:nth-child(2) {
  -webkit-animation-delay: -3s;
          animation-delay: -3s;
  -webkit-animation-duration: 10s;
          animation-duration: 10s;
}
.loginHeader .parallax > use:nth-child(3) {
  -webkit-animation-delay: -4s;
          animation-delay: -4s;
  -webkit-animation-duration: 13s;
          animation-duration: 13s;
}
.loginHeader .parallax > use:nth-child(4) {
  -webkit-animation-delay: -5s;
          animation-delay: -5s;
  -webkit-animation-duration: 20s;
          animation-duration: 20s;
}
@-webkit-keyframes move-forever {
  0% {
    -webkit-transform: translate3d(-90px, 0, 0);
            transform: translate3d(-90px, 0, 0);
  }
  100% {
    -webkit-transform: translate3d(85px, 0, 0);
            transform: translate3d(85px, 0, 0);
  }
}
@keyframes move-forever {
  0% {
    -webkit-transform: translate3d(-90px, 0, 0);
            transform: translate3d(-90px, 0, 0);
  }
  100% {
    -webkit-transform: translate3d(85px, 0, 0);
            transform: translate3d(85px, 0, 0);
  }
}
@media (max-width: 768px) {
  .loginHeader .waves {
    height: 40px;
    min-height: 40px;
  }
  .loginHeader .content {
    height: 30vh;
  }
  .loginHeader h1 {
    font-size: 24px;
  }
}

@keyframes zoomin {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@-webkit-keyframes zoomin {
  /* Safari and Chrome */
  from {
    -webkit-transform: scale(0);
  }
  to {
    -webkit-transform: scale(1);
  }
}

